@import url(//db.onlinewebfonts.com/c/073a3b73a63a87e100f6bb8f003fc0d3?family=Flexo+Light);
.modal-container{
  position: fixed;
  width: 100vw;
  height: 100%;
  margin: 0;
  background: var(--shadow-bg);
  cursor: pointer;
  z-index: 10;
}


.card{
  width: 90%;
  max-width: 750px;
  margin: 5rem auto;
  background: var(--gray-bg);
  border-radius: 1rem;
  cursor: auto;
}

.pokemon-card__title{
  display: flex;
  height: 3.5rem;
  padding-top: 1rem;
  padding-bottom: 1rem;  
}

.pokemon-card__compare{
  cursor: pointer;
  border-radius: 0.3rem;
  padding: 0.2rem 1rem;
  margin-left: 1.5rem;
  border: solid 1px var(--white);
  background: var(--redish);
  color: var(--white);
}

.pokemon-card__compare:hover,
.pokemon-card__compare:active{
  background: var(--white);
  color: var(--redish);
}

.pokemon-card__close{
  margin-left: auto; 
  margin-right: 1.5rem;
  border-radius: 50%;
  cursor: pointer;
  color: var(--redish);
  border: solid 1px var(--redish);
}




.pokemon-container{
  margin: 2rem 1.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: var(--white);
  border-radius: 1rem;
}

.image-container{
  width: 12rem;

  -ms-interpolation-mode: nearest-neighbor;

      image-rendering: -moz-crisp-edges;

      image-rendering: pixelated;
  margin-top: -6rem;
}

.pokemon-img{
  width: 100%;
}

.pokemon-name{
  margin: 0;
  color: var(--gray);
}

.pokemon-description{
  margin: 0.5rem 2.5rem;
  text-align: center;
  color: var(--light-black);
}

.pokemon-types{
  list-style: none;
  text-align: center;
  padding: 0;
}

.pokemon-type{
  display: inline-block;
  margin: 0 0.2rem;
  padding: 0.5rem 1rem;
  border: 1px solid var(--white);
  border-radius: 5px;
  color: var(--white);
  
}

.information-container{
  display: flex;
  width: 80%;
  text-align: center;
  margin-top: 1rem;
  justify-content: center;
  padding-bottom: 0.5rem;
}

.information-container__item{
  margin: 0;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
 
}

.bordered{
  border-right: rgba(112, 112, 112, 0.42) solid thin;
  border-left: rgba(112, 112, 112, 0.42) solid thin;
}

.information-container__item-value,
.information-container__item-name{
  font-size: 1rem;
  color: var(--light-black);
  font-weight: 500;
  margin: 0.5rem;
}

.information-container__item-value{
  color: var(--black);
}

