.main-nav-container{
  position: -webkit-sticky;
  position: sticky;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  top: 0;
  left: 0;
  z-index: 1;
}

.nav-bar{
  width: 100vw;
  height: 4rem;
  display: flex;
  justify-content: space-between;
  background: var(--redish);
  box-shadow: 0 8px 16px 0 var(--shadow-highligthed);
}

.nav-bar-container{
  display: flex;
  align-items: center;
  margin: 0;
  width: 100%;
}

.home-link{
  margin: 0;
  padding-left: 2rem;
}

.home-link-img{
  height: 2rem;
}

.normal-link{
  text-decoration: none;
  padding: 0;
  height: 1px;
  margin-left: 1rem;
  margin-bottom: 1rem;
  color: var(--white);
}

.search-input{
  margin: 1rem;
  padding-left: 1rem;
  border-radius: 0.5rem;
}

.mobile-bar-container{
    display: none;
}

.burger-button{
  margin-left: 1rem;
  display: none;
}

@media (max-width: 550px){

  .nav-bar-container{
    display: none;
  }

  .burger-button{
    display: inline;
    outline: none;
    cursor: pointer;
    border: transparent;
    background: transparent;
  }
  
  .burger-button-bar{
    display: block;
    width: 2rem;
    height: 2px;
    margin: 0.5rem 0;
    background: var(--white);
  }
  
  .mobile-bar-container{
    width: 100vw;
    padding: 1rem;
    display: flex;
    background: var(--white);
    flex-direction: column;
    box-shadow: 0 8px 16px 0 var(--shadow-highligthed);
  }
  
  .mobile-link{
    margin-top: 0.5rem;
    padding: 0.5rem;
    display: block;
    text-align: center;
    text-decoration: none;
    border-bottom: 1px solid var(--shadow);;
    color: var(--light-black);
    
  }
}


.pokemon__items{
  margin: 2rem;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  grid-gap: 1.5rem;
}


.pokemon__item{
  box-shadow: 0 4px 8px 0 var(--shadow);
  transition: 0.3s;
  border-radius: 1rem;
  cursor: pointer;
}

.pokemon__item:hover {
  box-shadow: 0 8px 16px 0 var(--shadow-highligthed);
}


.pokemon__item-img{
  width: 100%;
  background: var(--light-gray);
  border-top-left-radius: 1rem;
  -ms-interpolation-mode: nearest-neighbor;
      image-rendering: -moz-crisp-edges;
      image-rendering: pixelated;
  border-top-right-radius: 1rem;
}


.pokemon__item-name{
  text-align: center;
  color: var(--light-black);
  font-size: 1.3rem;
  font-weight: 600;
  text-transform: none;
  margin: 1rem;
}



@media (max-width: 500px){
  .pokemon__items{
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  }
  
}
.welcome-container{
  position: relative;
  background: url(/react-project1/static/media/pokedex-bg.1083716c.jpg);
  height: 100vh;
}


.welcome-card{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50vw;
  height: 50vh;
  background: var(--light-white);
  border-radius: 5rem;
}

.welcome-message-container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.welcome-message{
  font-size: 3.5rem;
  text-align: center;
  color: var(--light-black);
}

.welcome-button{
  text-decoration: none;
  color: var(--white);
  background: var(--redish);
  padding: 1rem 2rem;
  border-radius: 1rem;
}

.welcome-button:hover{
  color: var(--redish);
  background: var(--white);
  border: 1px solid var(--redish);
}

.welcome-img{
  position: absolute;
  width: 7rem;
  bottom: 5%;
  left: 5%;
}


@media (max-width: 900px){

  .welcome-card{
    width: 70vw;
    height: 50vh;
  }
  .welcome-img{
    width: 5rem;
  }

}


@media (max-width: 600px){

  .welcome-card{
    width: 80vw;
    height: 50vh;
  }
  
  .welcome-message{
    font-size: 3rem;
  }
  
  .welcome-button{
    text-decoration: none;
    color: var(--white);
    background: var(--redish);
    padding: 0.8rem 1.5rem;
    border-radius: 1rem;
  }
  .welcome-img{
    width: 4rem;
  }
}
.main-container{
  height: 100vh;
  position: relative;
}

.container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
