}


.sorry-title{
  font-size: 2.5rem;
  margin: 0;
}


.not-found-number{
  display: inline-block;
  margin: 0;
  padding: 1rem;
  font-size: 10rem;
  font-weight: 500;
}

.not-found-img{
  width: 8rem;
}

.page-not-found{
  font-size: 1.5rem;
  margin: 0;
}

.back-button{
  margin-top: 3rem;
  padding: 1rem;
  cursor: pointer;
  color: var(--black);
  font-weight: 600;
  text-decoration: none;
  border: 1px solid var(--gray);
  border-radius: 0.8rem;

}

.sad-pokemon-img{
  position: absolute;
  width: 6rem;
  left: 2rem;
  bottom: 2rem;
}

@media (min-width: 550px){
  .sorry-title{
    font-size: 3rem;
  }
  
  
  .not-found-number{
    font-size: 15rem;
  }
  
  .not-found-img{
    width: 11rem;
  }
  
  .page-not-found{
    font-size: 2rem;
    margin: 0;
  }
   
  .sad-pokemon-img{
    width: 8rem;
  }

  .back-button{
    padding: 1rem 2rem;
  }
}

@media (min-width: 800px){
  .sorry-title{
    font-size: 3.5rem;
  }
  
  
  .not-found-number{
    font-size: 20rem;
  }
  
  .not-found-img{
    width: 16rem;
  }
  
  .page-not-found{
    font-size: 2.5rem;
    margin: 0;
  }
   
  .sad-pokemon-img{
    width: 10rem;
  }

  .back-button{
    padding: 1rem 3rem;
  }
}



:root{
  --shadow: #00000040;
  --shadow-bg: #00000099;
  --shadow-highligthed: #00000080;
  --light-black: #313131;
  --gray: #808080;
  --light-gray: #EBEBEB;
  --gray-bg: #dbdbdb;
  --white: #FFFFFF;
  --light-white: #FFFFFF70;
  --redish: #BE2323;

  --bug: #A8B820;
  --dark: #705848;
  --dragon: #7038F8;
  --electric: #F8D030;
  --fairy: #EE99AC;
  --fighting: #C03028;
  --fire: #F08030;
  --flying: #A890F0;
  --ghost: #705898;
  --grass: #78C850;
  --ground: #E0C068;
  --ice: #98D8D8;
  --normal: #A8A878;
  --poison: #A040A0;
  --psychic: #F85888;
  --rock: #B8A038;
  --steel: #B8B8D0;
  --water: #6890F0;
  --unknown: #68A090;
}

*{
  box-sizing: border-box;
}

body{
  margin: 0;
  font-family: 'Flexo-Demi' , arial, sans-serif;
